import React from 'react'
import { Link, useParams } from 'react-router-dom'

const Category = ({ shopList }) => {
  const { cate } = useParams();
  //url            
  const cateList = shopList.filter(el => String(cate) === el.category)
  return (
    <section className='shopList'>
      <div className="inner">
        {
          cateList.map((el, idx) => {
            return (
              <figure key={el.id}>
                <Link to={"/shopItem/" + el.id}>
                  <div className="box">
                    <img src={el.src} alt="" />
                  </div>
                  <div className="name">{el.name}</div>
                  <div className="des">{el.des.substring(0, 50)}</div>
                  <div className="price"><span>{el.price.toLocaleString()}</span></div>
                </Link>
              </figure>
            )
          })
        }
      </div>
    </section>
  )
}

export default Category